.review {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  user-select: none;

  &__name {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 12px;

    &>span:nth-child(1) {
      font-weight: 700;
    }

    &>span {
      width: 100%;
    }
  }

  &__img {
    border: 2px solid white;
    box-sizing: border-box;
    filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
    border-radius: 32px;
    max-width: 48px;
    height: 48px;
    margin-right: 10px;
    margin-bottom: 12px;
    &>img {
      width: 100%;
      height: 100%;
      border-radius: 32px;
      object-fit: cover;
    }
  }

  &__text {
    display: flex;
    justify-content: flex-start;
    padding-left: 2px;
    &>.like-button {
      margin-right: 14px;
    }
  }


}